// src/lib/auth/options.js
import OIDCProvider from "next-auth/providers/oidc"
import CredentialsProvider from "next-auth/providers/credentials"

const providers = []

providers.push(
  CredentialsProvider({
    id: "sso",
    name: "SSO",
    credentials: {},
    async authorize() {
      return {
        id: "demo-sso-user",
        name: "Demo SSO User",
        email: "demo.sso@example.com",
        image: "https://www.gravatar.com/avatar/00000000000000000000000000000000?d=mp&f=y",
      }
    },
  }),
)

const issuer = process.env.AUTH_OIDC_ISSUER
const clientId = process.env.AUTH_OIDC_ID
const clientSecret = process.env.AUTH_OIDC_SECRET

if (issuer && clientId && clientSecret) {
  providers.push(
    OIDCProvider({
      id: "oidc",
      name: "OIDC",
      issuer,
      clientId,
      clientSecret,
      checks: ["pkce", "state"],
    }),
  )
}

export const authOptions = {
  session: {
    strategy: "jwt",
  },
  pages: {
    signIn: "/login",
  },
  providers,
  callbacks: {
    async jwt({ token, user }) {
      if (user) {
        token.user = {
          id: user.id ?? token.sub ?? "demo-sso-user",
          name: user.name ?? token.user?.name ?? "",
          email: user.email ?? token.user?.email ?? "",
          image: user.image ?? token.user?.image ?? "",
        }
      }
      return token
    },
    async session({ session, token }) {
      if (token?.user) {
        session.user = token.user
      }
      return session
    },
  },
  debug: process.env.NODE_ENV === "development",
}
